import { Layout } from 'components';
import { totalPriceItem, formatCurrency, projection } from 'Functions';
import OrderListItem from 'components/Order/OrderListItem/OrderListItem';

import {
  OrderWrap,
  Title,
  OrderContent,
  OrderList,
  Form,
  Input,
  Total,
  Button,
  EmptyList,
} from './Order.style';

const rulesData = {
  name: ['name'],
  price: ['price'],
  count: ['count'],
  topping: [
    'topping',
    arr => arr.filter(obj => obj.checked).map(obj => obj.name),
    arr => (arr.length ? arr : 'no topping'),
  ],
  choice: ['choice', item => (item ? item : 'No choices')],
};

const Order = ({
  orders,
  setOrders,
  setOpenItem,
  authentication,
  logIn,
  dataBase,
}) => {
  const total = orders.reduce((total, item) => totalPriceItem(item) + total, 0);
  const totalCounter = orders.reduce((total, item) => total + item.count, 0);

  const sendOrder = () => {
    const newOrder = orders.map(projection(rulesData));
    dataBase.ref('orders').push().set({
      nameClient: authentication.displayName,
      email: authentication.email,
      order: newOrder,
    });
    setOrders([]);
  };

  const deleteItem = idx =>
    setOrders(orders.filter((item, index) => index !== idx));

  return (
    <Layout>
      <OrderWrap>
        <Title>ÐÐ°Ñ Ð·Ð°ÐºÐ°Ð·</Title>
        <OrderContent>
          {orders.length > 0 ? (
            <OrderList>
              {orders.map((order, index) => (
                <OrderListItem
                  key={order.id + order.name}
                  order={order}
                  onDelete={deleteItem}
                  setOpenItem={setOpenItem}
                  index={index}
                />
              ))}
            </OrderList>
          ) : (
            <EmptyList>Ð¡Ð¿Ð¸ÑÐ¾Ðº Ð·Ð°ÐºÐ°Ð·Ð¾Ð² Ð¿ÑÑÑ</EmptyList>
          )}

          <Form>
            <Input type="text" name="" />
            <Input type="text" name="" />
            <Input type="text" name="" />
          </Form>
        </OrderContent>
        <Total>ÐÑÐµÐ³Ð¾ ÑÐ¾Ð²Ð°ÑÐ¾Ð² Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ: {totalCounter}</Total>
        <Total>Ð¡ÑÐ¼Ð¼Ð° Ð·Ð°ÐºÐ°Ð·Ð°: {formatCurrency(total)}</Total>
        <Button
          onClick={() => {
            if (authentication) {
              sendOrder();
            } else {
              logIn();
            }
          }}
        >
          ÐÑÐ¾ÑÐ¼Ð¸ÑÑ Ð·Ð°ÐºÐ°Ð·
        </Button>
      </OrderWrap>
    </Layout>
  );
};

export default Order;
